import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlpha, onlyNumber, WindowComponent } from 'summer';
import { CepPais, CLASS_NAME } from '../../../../models/dtos/ger/cepPais';

interface CepPaisEditViewProps extends EditViewProps<CepPais> {}

const CepPaisEditView: FC<CepPaisEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CepPais>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de País">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline
          id="codigo"
          label="Código"
          formProps={{ ...formProps, validation: onlyNumber(0, 3) }}
          tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
          isPrependLabel={true}
          placeholder="Informe o código"
          type={InputType.NUMBER}
        />

        <InputGroupInline
          id="descricao"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
          label="Descrição"
          placeholder="Informe a descrição"
        />

        <InputGroupInline
          id="codigoIBGE"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: onlyNumber(0, 4) }}
          label="Código IBGE"
          placeholder="Informe o código IBGE"
          type={InputType.NUMBER}
        />

        <InputGroupInline
          id="sigla"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: onlyAlpha(0, 2) }}
          label="Sigla"
          placeholder="Informe a Sigla"
        />
      </EditForm>
    </WindowComponent>
  );
};

export default CepPaisEditView;
